import interfaces from '../../../utils/interfaces.js';
export default {
	data() {
		return {
			categoryList: [],
			currentNavigation: 0
		}
	},
	onLoad() {
		this.initData()
	},
	methods: {
		handleCategory(index) {
			this.currentNavigation = index
		},
		handleCategoryList(item) {
	
			uni.navigateTo({
				url: '../../goods/goodsList?name=' + item.name
			})
		},
		initData() {
			this.request({
				url: interfaces.getCategory,
				success: (res) => {
					this.categoryList = res.data;
				}
			})
		}
	}
}
